import { gql } from '@apollo/client';

export const GETPRODUCTBYID = gql`
  query productById($id: UUID!) {
    productById(id: $id) {
      projectId
      memberId
      title
      photo
      slug
      sku
      price
      offerPrice
      stock
      active
      seoProduct
      shippingType
      shippingPrice
      description
      publishDate
      shopType
      views
      status
      seoProduct
      categories {
        name
        id
        parentId
        subCategories {
          name
          parentId
          id
        }
      }
      images {
        id
        title
        original
      }
      id
      createdAt
      updatedAt
    }
  }
`;
